import React from 'react';
import countryList from 'country-list';

interface BasicInfoStepProps {
  step: number;
  currentStep: number;
}

export default function BasicInfoStep({
  step,
  currentStep,
}: BasicInfoStepProps) {
  const countriesOptions = countryList
    .getData()
    .map(({ code, name }) => <option key={code}>{name}</option>);
  return (
    <div style={{ display: step === currentStep ? `inline` : `none` }}>
      <label className="block mt-5" htmlFor="firstName">
        <span className="text-gray-700">What's your first name?</span>
        <span className="text-rose-500">*</span>
        <input
          type="text"
          id="firstName"
          name="first_name"
          className="
                    mt-1
                    block
                    w-full
                    rounded-md
                    bg-gray-100
                    border-transparent
                    focus:border-gray-500 focus:bg-white focus:ring-0
                  "
          placeholder="e.g. Satoshi"
        />
      </label>
      <label className="block mt-5" htmlFor="lastName">
        <span className="text-gray-700">And your last name</span>
        <input
          type="text"
          id="lastName"
          name="last_name"
          className="
                    mt-1
                    block
                    w-full
                    rounded-md
                    bg-gray-100
                    border-transparent
                    focus:border-gray-500 focus:bg-white focus:ring-0
                  "
          placeholder="e.g. Nakamoto"
        />
      </label>
      <label className="block mt-5">
        <span className="text-gray-700">What's your country of residence?</span>
        <select
          placeholder="Choose"
          name="country_of_residence"
          className="
                    mt-1
                    block
                    w-full
                    rounded-md
                    bg-gray-100
                    border-transparent
                    focus:border-gray-500 focus:bg-white focus:ring-0
                  "
        >
          <option>Choose...</option>
          {countriesOptions}
        </select>
      </label>
    </div>
  );
}
